import { useState } from "react";
import { Box, Paper, Typography, IconButton, List, ListItem, ListItemText } from "@mui/material";
import { motion } from "framer-motion";
import { FiChevronLeft, FiChevronRight } from "react-icons/fi";
import CustomTable from "@/app/components/CustomTable";

export default function TaskTracker() {
  const [selectedTask, setSelectedTask] = useState(null);
  const [leftOpen, setLeftOpen] = useState(true);

  const tasks = [
    { id: 1, name: "Project A" },
    { id: 2, name: "Project B" },
    { id: 3, name: "Project C" },
  ];

  const columns = [
    { key: "id", label: "ID" },
    { key: "task", label: "Task" },
    { key: "owner", label: "Owner" },
    { key: "status", label: "Status" },
  ];

  const data = [
    { id: 1, task: "Design UI", owner: "John", status: "In Progress" },
    { id: 2, task: "Backend API", owner: "Jane", status: "Pending" },
  ];

  return (
    <Box sx={{ display: "flex", height: "calc(100vh - 32px)", gap: 2, p: 2 }}>
      {/* Left Panel */}
      <motion.div
        animate={{ width: leftOpen ? 260 : 0 }}
        transition={{ duration: 0.5, ease: [0.4, 0, 0.2, 1] }}
      >
        {leftOpen && (
          <Paper sx={{ height: "100%", p: 2, overflowY: "auto", borderRadius: 2, boxShadow: "0 4px 16px rgba(0,0,0,0.06)" }}>
            <Typography sx={{ fontWeight: 600, mb: 2 }}>Projects</Typography>
            <List>
              {tasks.map((task) => (
                <ListItem
                  key={task.id}
                  button
                  selected={selectedTask?.id === task.id}
                  onClick={() => setSelectedTask(task)}
                  sx={{
                    borderRadius: 1,
                    mb: 1,
                    "&.Mui-selected": { bgcolor: "#f0f0f0" },
                  }}
                >
                  <ListItemText primary={task.name} />
                </ListItem>
              ))}
            </List>
          </Paper>
        )}
      </motion.div>

      {/* Toggle Button */}
      <Box sx={{ display: "flex", alignItems: "center" }}>
        <IconButton onClick={() => setLeftOpen(!leftOpen)}>
          {leftOpen ? <FiChevronLeft /> : <FiChevronRight />}
        </IconButton>
      </Box>

      {/* Right Panel */}
      <motion.div
        animate={{ flex: 1 }}
        transition={{ duration: 0.5, ease: [0.4, 0, 0.2, 1] }}
      >
        <Paper sx={{ height: "100%", p: 3, borderRadius: 2, boxShadow: "0 4px 16px rgba(0,0,0,0.06)" }}>
          <Typography sx={{ fontWeight: 600, mb: 2 }}>
            {selectedTask ? selectedTask.name : "Select a Project"}
          </Typography>
          {selectedTask && <CustomTable columns={columns} data={data} />}
        </Paper>
      </motion.div>
    </Box>
  );
}
